import test from 'ava';

test('foo', t => {
    var java = require("java");
    java.classpath.push("class");
    var instance = java.newInstanceSync("ABC");
    java.callMethod(instance, "add",3,3,function(err, results) {
        if(err) { console.error(err); return; }
        t.is(results,6);
    });
});

